<template>
    <div class="pos">
        <div>
            <el-row id='order-list'>
                <el-col :span='7' class="pos-order">
                    <el-tabs>
                        <el-tab-pane label="点餐"  class="order">
                            <el-table :data="tableData">
                                <el-table-column prop='goodsName' label="商品名称">

                                </el-table-column>
                                <el-table-column prop='count' label="数量">

                                </el-table-column>
                                <el-table-column prop='price' label="金额">

                                </el-table-column>
                                <el-table-column label="操作">
                                    <template slot-scope="scope">
                                        <el-button type="text" size="small" @click='delSingleGood(scope.row)'>删除</el-button>
                                        <el-button type="text" size="small" @click='addfood(scope.row)'>增加</el-button>
                                        <!-- scope.row表示对应行的数据。也是一一对于的，有goodsName,数量以及价格 -->
                                    </template>
                                </el-table-column>
                            </el-table>
                            <div class="count">
                                数量：<span>{{totalCount}}</span>
                                金额：<span>{{totalMoney}}</span>元
                            </div>
                            <div class="choice">
                                <el-button type="warning">挂单</el-button>
                                <el-button type="danger" @click="delAllGoods()">删除</el-button>
                                <el-button type="success" @click="checkout()">结账</el-button>
                            </div>
                        </el-tab-pane>
                        <el-tab-pane label="挂单">挂单</el-tab-pane>
                        <el-tab-pane label="外卖">外卖</el-tab-pane>

                    </el-tabs>
                    
                </el-col>
                <el-col :span='17'>
                    <div class="hot">
                        <h3>常用商品</h3>
                        <div class="foodlist">
                            <ul>
                                <li v-for="item in oftenGoods" @click='addfood(item)'><span class="name">{{item.goodsName}}</span><span class="price">{{item.price}}</span></li>
                            </ul>
                        </div>
                    </div>
                    <div class="sort">
                        <el-tabs>
                            <el-tab-pane label="汉堡">
                                <div class="detail" v-for="item in type0Goods" @click='addfood(item)'>
                                    <!-- 由于选项卡中有四个分类，所以数据源要区别开· -->
                                    <div class="img">
                                        <img :src="item.goodsImg" alt="">
                                        <!-- 此处由于是绑定属性，所以img :src="item.goodsImg"中的src前要加冒号 -->
                                    </div>
                                    <div class="des">
                                        <div class="name">{{item.goodsName}}</div>
                                        <div class="price">{{item.price}}</div>
                                    </div>
                                </div>
                                
                            </el-tab-pane>
                            <el-tab-pane label="小食">
                                <div class="detail" v-for="item in type1Goods" @click='addfood(item)'>
                                    <!-- 由于选项卡中有四个分类，所以数据源要区别开· -->
                                    <div class="img">
                                        <img :src="item.goodsImg" alt="">
                                        <!-- 此处由于是绑定属性，所以img :src="item.goodsImg"中的src前要加冒号 -->
                                    </div>
                                    <div class="des">
                                        <div class="name">{{item.goodsName}}</div>
                                        <div class="price">{{item.price}}</div>
                                    </div>
                                </div>
                            </el-tab-pane>
                            <el-tab-pane label="饮料">
                                <div class="detail" v-for="item in type2Goods" @click='addfood(item)'>
                                    <!-- 由于选项卡中有四个分类，所以数据源要区别开· -->
                                    <div class="img">
                                        <img :src="item.goodsImg" alt="">
                                        <!-- 此处由于是绑定属性，所以img :src="item.goodsImg"中的src前要加冒号 -->
                                    </div>
                                    <div class="des">
                                        <div class="name">{{item.goodsName}}</div>
                                        <div class="price">{{item.price}}</div>
                                    </div>
                                </div>
                            </el-tab-pane>
                            <el-tab-pane label="套餐">
                                <div class="detail" v-for="item in type3Goods" @click='addfood(item)'>
                                    <!-- 在进行数据的循环时，最好用一样的变量（如此处的item）来接收循环的到的结果，这样在传递方法时，就都是使用一样的参数 -->
                                    <!-- 由于选项卡中有四个分类，所以数据源要区别开· -->
                                    <div class="img">
                                        <img :src="item.goodsImg" alt="">
                                        <!-- 此处由于是绑定属性，所以img :src="item.goodsImg"中的src前要加冒号 -->
                                    </div>
                                    <div class="des">
                                        <div class="name">{{item.goodsName}}</div>
                                        <div class="price">{{item.price}}</div>
                                    </div>
                                </div>
                            </el-tab-pane>
                        </el-tabs>
                    </div>
                </el-col>
            </el-row>
        </div>
    </div>
</template>
<script>
import axios from 'axios'
    export default{
        data(){
            return {
                    tableData:[],
                    oftenGoods:[],
                    type0Goods:[],
                    type1Goods:[],
                    type2Goods:[],
                    type3Goods:[],
                    totalMoney:0,
                    totalCount:0
            }
                
        },
        mounted(){
            // 因为element是虚拟的，所以要在所有的虚拟dom加载完成后再获取到对于的高度，解决100%高度问题
            //mounted就是在所有虚拟dom加载完之后
            var orderHeight=document.body.clientHeight; //获取body的高度
            //console.log(orderHeight) 测试获取高度是否成功
            document.getElementById("order-list").style.height=orderHeight+'px';
        },
        created(){
            axios.get('https://www.easy-mock.com/mock/5b8b30dbf032f03c5e71de7f/kuaican/oftenGoods')
            .then(res=>{
                // console.log(res)
                this.oftenGoods=res.data
            })
            .catch(err=>{
                console.log(error)
            }),
            axios.get('https://www.easy-mock.com/mock/5b8b30dbf032f03c5e71de7f/kuaican/typeGoods')
            .then(res=>{
                // console.log(res)
                this.type0Goods=res.data[0];
                this.type1Goods=res.data[1];
                this.type2Goods=res.data[2];
                this.type3Goods=res.data[3];



            })
            .catch(error=>{
                console.log(error)
            })
        },
        methods:{
            // 要添加商品，所以要传入添加的商品对象
            addfood(goods){
                //每次进行添加时，将数量与金额设置为0
                this.totalMoney=0;
                this.totalCount=0;
                //传入的商品是否存在与订单列表中
                let isHave=false;   //默认设定订单列表中新传入的商品不存在
                for(let i=0;i<this.tableData.length;i++){
                    //此处要通过this才能获取到tableData
                    //为每一个食物定义一个goodsId，当传入食物的goodsId与订单列表里的食物ID相同时，就只增加数量
                    if(this.tableData[i].goodsId==goods.goodsId){
                        // 注意不是this.tableData.goodsId==goods.goodsId
                        isHave=true;
                    }
                }
                // 根据判断的值编写业务逻辑
                if(isHave){
                    //改变订单列表中商品的数量
                    //找到存在的商品在列表中的位置。对tableData中的数据进行筛选，将筛选出来的数据用一个新的数组存储
                    let arr = this.tableData.filter(o=>o.goodsId == goods.goodsId);
                    // arr[0].count++;
                    arr[0].count++;
                    // console.log(arr);
                    
                }else{
                    //构造一个新的对象用于存储添加的商品，由于商品有id,name,price，所以要使用对象来进行存储
                    let newGoods={
                        goodsId:goods.goodsId,
                        goodsName:goods.goodsName,
                        price:goods.price,
                        count:1
                    }
                    //将定义好的新对象添加到数组中
                    this.tableData.push(newGoods)
                }
                // 调用定义好的函数。注意此处要用this
                    this.summary();
                
            },
            // 删除单个商品。传入要删除的商品
            delSingleGood(goods){
                // 对tableData进行从新赋值。使用数组的filter方法筛选出除了要被删除商品的其他所有商品
                this.tableData=this.tableData.filter(o=>o.goodsId != goods.goodsId);
                this.summary();
            },
            // 删除所有商品。实际就是将tableData清空，以及totalMoney和totalCount清零
            delAllGoods(){
                this.tableData=[],
                this.totalMoney=0;
                this.totalCount=0;
            },
            //模拟结账
            checkout(){
                //当tableData里有值时才能进行结账
                if(this.totalCount != 0){
                    //清空所有数据
                    this.tableData=[],
                    this.totalMoney=0;
                    this.totalCount=0;
                    // element中数据的写入
                    this.$message({
                        message:'结账成功，感谢你对本店的关照',
                        type:'success',

                    })
                }else{
                    this.$message.error('不能空结账')
                }
            },
            //当添加和删除时，总的数量与金额都会发生改变，所以用一个专门的函数来实现相同代码的复用
            summary(){
                this.totalCount=0;
                this.totalMoney=0;
                // 当tableData里有数据时才进行数量与金额的计算
                if(this.tableData){
                    this.tableData.forEach(element=>{
                    this.totalCount+=element.count;
                    this.totalMoney+=element.price*element.count;
                })
                }
            }
        }
       
    }
</script>
<style>
    .pos-order{
        background: #f9fafc;
        border-right:1px solid #becbdc;
        height: 100%;
        /* 设置以上三个后pos-order不能100%显示高度，原因是在页面中使用了Element组件，
        这样他会自动给我们生产虚拟DOM，我们无法设置高度100%； */
    }
    .choice{
        margin:20px;
    }
    .hot{
        border-bottom: 1px solid red;
        overflow: hidden;
    }

    .hot h3{
        margin: 0;
        font-size: 20px;
        height: 38px;
        line-height: 38px;
        padding-left: 10px;
        background: #f9fafc;
        /* width:100%; */
        text-align: left;
        border-bottom: 1px solid #e4e7ee;
    }
    .hot .foodlist ul{
        margin: 0;
        padding: 0;
        list-style: none;
        cursor: pointer;
    }
    .hot .foodlist ul li{
        float: left;
        background: #fff;
        font-size: 16px;
        padding: 5px;
        border-radius: 5px;
        margin:10px;

    }
    .hot .foodlist ul li span.name{
        display: inline-block;
        margin-right: 5px;
        
    }
    .hot .foodlist ul li span.price{
        display: inline-block;
        color:#81ceff;
        
    }
    .sort{
        margin-left: 10px;
        overflow: hidden;
        /* background: #000; */
        height: 100%;
    }
    .sort .detail{
        background: #fff;
        overflow: hidden;
        width:150px;
        float: left;
        margin-right:20px;
        margin-bottom:20px;
        cursor: pointer;
    }
    .sort .img img{
        display: block;
        width:50px;
        height: 50px;
        float: left;
        margin-right: 5px;
    }
    .sort .des{
        float: left;
    }
    .sort .des div.name{
        color:red;
    }
</style>



