<template>
  <div id="app">
    <leftNav></leftNav>
    <div class="main">
        <!-- 添加了router-view后，由于给pos组件设置了路由，所以pos组件的内容都将在被类名为main的标签包裹起来显示 -->
        <router-view/>
    </div>
    
    <!-- <router-view/>对设置了路由的组件有影响，对定义的组件如leftNav没有影响 -->
  </div>
</template>

<script>
import leftNav from '@/components/common/leftNav'
export default {
  name: 'App',
  components:{
    leftNav
  }
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}
.main{
  float:left;
  width:95%; 
  background-color: #EFF2F7;
  height:100%;
  overflow: auto;
}
</style>



